<template>
  <div class="continued-view bte8">
    <ul class="product-list container">
      <li class="mark" v-if="detail.mark">
        <span>{{detail.mark|mark}}</span>
        <img src="../../assets/pay/mark.png" alt="">
      </li>
      <li class="list-one">{{detail.productName}}</li>
      <li class="flex-bet list-two">
        <span>{{detail.interestRate*100|thousand}}</span>
        <span>{{detail.deadline|deadLine}}</span>
        <span>{{detail.investmentAmount|thousand}}</span>
      </li>
      <li class="flex-bet list-three">
        <span> (%)</span>
        <span> ({{detail.deadline|deadWord}})</span>
        <span> ()</span>
      </li>
    </ul>
    <div class="bank flex-bet" v-if="isCard" @click="changeCard()">
      <div class=" flex-bet">
        <img :src="pay.icon" class="bank-logo">
        <div class="bank-num">
          <p>
            <span class="bank-name">{{pay.bankName}}</span>
            <span class="last-num">({{pay.bankCard|lastNum}})</span>
          </p>
          <p class="bank-tip">{{pay.singleLimited/10000}}{{pay.dayLimited/10000}}</p>
        </div>
      </div>
      <i class="fa fa-angle-right"></i>
    </div>
    <div class="bank flex-bet" v-if="!isCard" @click="addCard()">
      <div class=" flex-bet">
        <img src="../../assets/pay/add.png" alt="">
        <div class="bank-num">
          <p class="bank-name"></p>
          <p class="bank-tip"></p>
        </div>
      </div>
      <i class="fa fa-angle-right"></i>
    </div>
    <div class="input-money container">
      <span></span>
      <input type="text" v-model="inputMoney" @input="input()" placeholder="">
    </div>
    <p class="agree-line">
      <label for="choose" v-if="isChoose">
        <img src="../../assets/money/agree.png" alt="">
      </label>
      <label for="choose" v-else>
        <img src="../../assets/money/refuse.png" alt="">
      </label>
      <input type="checkbox" id="choose" v-model="isChoose">
      <span class="agree"></span>
      <span class="blue" @click="checkContract()"></span>
    </p>
    <transition name="slide-fade">
      <div class="msg-box tc" v-show="isInteger">
        <span class="msg">{{tip}}</span>
      </div>
    </transition>
    <div class="bottom-money flex-bet container">
      <p>
        <span></span>
        <span class="money-num">{{inputMoney|thousand}}</span>
      </p>
      <button :disabled="!isChoose" :class="[{'btn-grey':!isChoose}, 'btn']" @click="confirm()"></button>
    </div>
    <transition name="slide-fade">
      <div class="add-alert alert" @click.self="boxHide()" v-if="chooseCard">
        <div class="card-box">
          <div class="add-bank type tc">
            
            <i class="fa fa-close" @click="close()"></i>
          </div>
          <div class="add-bank flex-bet" :class="{'grey-bg':cardList.isOne}" @click="clickCard('isOne')">
            <div class=" flex-bet">
              <img :src="pay.icon" class="bank-logo">
              <div class="bank-num">
                <p>
                  <span class="bank-name">{{pay.bankName}}</span>
                  <span class="last-num">({{pay.bankCard|lastNum}})</span>
                </p>
                <p class="bank-tip">{{pay.singleLimited/10000}}{{pay.dayLimited/10000}}</p>
              </div>
            </div>
            <i class="fa fa-check" v-show="cardList.isOne"></i>
          </div>
          <div class="add-bank flex-bet" v-if="isTwo" :class="{'grey-bg':cardList.isTwo}" @click="clickCard('isTwo')">
            <div class=" flex-bet">
              <img :src="payTwo.icon" class="bank-logo">
              <div class="bank-num">
                <p>
                  <span class="bank-name">{{payTwo.bankName}}</span>
                  <span class="last-num">({{payTwo.bankCard|lastNum}})</span>
                </p>
                <p class="bank-tip">{{payTwo.singleLimited/10000}}{{payTwo.dayLimited/10000}}</p>
              </div>
            </div>
            <i class="fa fa-check" v-show="cardList.isTwo"></i>
          </div>
          <div class="add-bank flex-bet" v-if="!isTwo" @click="addCard()">
            <div class=" flex-bet">
              <img src="../../assets/pay/addTwo.png" alt="">
              <div class="bank-num">
                <p class="bank-name"></p>
                <p class="bank-tip"></p>
              </div>
            </div>
            <i class="fa fa-angle-right"></i>
          </div>
        </div>
      </div>
    </transition>
  </div>
</template>

<script>
export default{
  data () {
    return {
      id: '',
      detail: {}, // 
      params: {}, // 
      tip: '', // 
      isCard: false, // ,
      isTwo: false, // 
      chooseCard: false, // 
      cardList: {
        isOne: false, // 1 
        isTwo: false // 2 
      }, // 
      isChoose: true, // 
      isInteger: false, // 
      inputMoney: '', // 
      pay: undefined, // 
      payTwo: undefined, // 2
      back: () => { // 
        this.$router.back(-1)
      }
    }
  },
  created () {
    this.getProduct() // 
    this.$http(this.path.productDetail(this.id)).then(res => {
      console.log(res.data.code)
      if (res.data.code === 0) {
        this.detail = res.data.data
        console.log(this.detail)
      }
    })
    this.$http(this.path.inputMoney).then(res => { // 
      console.log(res.data.code)
      if (res.data.code === 0) {
        this.pay = res.data.data[0] // 1z
        console.log(this.pay)
        if (this.pay.id) { // 
          this.isCard = true // ,1
        } else {
          this.isCard = false // 
        }
        if (res.data.data.length > 1) {
          this.payTwo = res.data.data[1] // 2
          console.log(this.payTwo)
        } else {
          this.payTwo = undefined
        }
      }
    })
    this.$parent.head.title = ''
    this.$parent.head.isWhite = false
    this.$parent.head.isEnter = false
    this.$parent.head.isArrow = false
    this.cardList.isOne = true // 1
  },
  methods: {
    input () { // 
      this.inputMoney = this.inputMoney.replace(/\D/g, '')
    },
    getProduct () { // id
      this.id = this.$route.query.id
      console.log(this.$route.query)
      if (this.$route.query.isBack) {
        this.$parent.head.back = () => { // 
          this.$router.push({name: 'main.money'})
        }
      }
    },
    confirm () { // 
      if (this.pay === undefined) {
        this.isInteger = true
        this.tip = ''
        return
      }
      console.log(this.inputMoney % 10000)
      if (this.inputMoney % 10000 !== 0 | this.inputMoney === '') { // 10000
        this.tip = '10000'
        this.isInteger = true // 
      } else { // 
        this.isInteger = false // 
        this.params = {
          productId: this.detail.id, // id
          money: this.inputMoney, // 
          bankCardId: this.pay.id, // idthis.pay.id
          userSign: undefined // 
        }
        console.log(this.params)
        sessionStorage.setItem('payMsg', JSON.stringify(this.params))
        this.$router.push({ // 
          name: `contract`,
          params: {
            productId: this.detail.id, // id
            money: this.inputMoney, // 
            bankCardId: this.pay.id, // idthis.pay.id
            userSign: undefined // 
          }
        })
      }
    },
    addCard () { // 
      this.$router.push({
        name: 'myAddBank'
      })
    },
    changeCard () { // 
      this.chooseCard = true // 1
      if (this.payTwo) { // 2
        this.isTwo = true // 2
      } else { // 2
        this.isTwo = false // 2
      }
    },
    clickCard (value) { // 
      for (let key in this.cardList) {
        if (value === key) { //  
          console.log(value)
          this.cardList[key] = true
          this.chooseCard = false // 
        } else { // 
          this.cardList[key] = false
        }
      }
      if (value === 'isOne') { // 1
        console.log(1)
        this.isOne = this.isOne
      } else { // 2
        console.log(2)
        this.isOne = this.isTwo
      }
    },
    boxHide () { // 
      this.chooseCard = false
    },
    close () { // 
      this.chooseCard = false
    },
    checkContract () { // 
      this.$router.push({ // 
        name: `contract`,
        query: {
          type: 2
        }
      })
    }
  }
}
</script>

<style lang="scss" scoped>
.continued-view {
  @include center($head-h, $base-h);
  font-family: "PingFang-SC-Medium";
  background: $base-bg;
  .product-list {
    position: relative;
    margin: 0.1rem 0;
    background: #fff;
    .list-one {
      line-height: 0.5rem;
    }
    .list-two {
      line-height: 0.1rem;
      font-size: 0.15rem;
      & span:first-child {
        font-size: 0.24rem;
        color: $title-color;
      }
      & span:nth-child(1)::after {
        content: "";
        position: absolute;
        left: 33.3%;
        height: 0.35rem;
        width: 0.01rem;
        background: #e8e8e8;
      }
      & span:nth-child(2)::after {
        content: "";
        position: absolute;
        left: 66.66%;
        height: 0.35rem;
        width: 0.01rem;
        background: #e8e8e8;
      }
    }
    .list-three {
      line-height: 0.5rem;
      font-size: 0.13rem;
      color: $tip-color;
    }
  }
  .mark {
    height: 0;
    color: #fff;
    text-align: right;
    font-size: 0.1rem;
    span {
      position: absolute;
      top: 0;
      right: 0.24rem;
    }
  }
  .bank {
    padding: 0.2rem;
    line-height: 0.25rem;
    background: #fff;
  }
  .input-money {
    margin: 0.1rem 0;
    line-height: 0.6rem;
    background: #fff;
    input {
      width: 2.2rem;
      text-indent: 0.1rem;
    }
    input::-webkit-input-placeholder {
      color: $list-color;
      font-size: 0.12rem;
    }
  }
  .agree-line {
    padding: 0 0.2rem;
    input {
      display: none;
    }
    .agree {
      font-size: 0.12rem;
      color: $tip-color;
    }
    .blue {
      font-size: 0.12rem;
      color: $agree-color;
    }
  }
  .msg-box {
    margin-top: 1rem;
  }
  .bottom-money {
    position: fixed;
    bottom: 0;
    left: 0;
    right: 0;
    line-height: 0.6rem;
    font-size: 0.15rem;
    background: #fff;
    .money-num {
      color: #d7a863;
    }
    .btn {
      width: 1rem;
    }
  }
  .add-alert {
    .card-box {
      position: absolute;
      left: 0;
      right: 0;
      bottom: 0;
    }
    .add-bank {
      padding: 0.05rem 0.2rem;
      line-height: 0.2rem;
      background: #fff;
      .fa-check {
        font-size: 0.2rem;
        color: $check-color;
        -webkit-text-stroke: 0.02rem #f6f6f6;
      }
    }
    .type {
      line-height: 0.4rem;
      color: $tip-color;
      font-size: 0.16rem;
      .fa-close {
        float: right;
        line-height: 0.35rem;
        font-size: 0.2rem;
        -webkit-text-stroke: 0.02rem #fff;
      }
    }
  }
  .bank-logo {
    width: 0.355rem;
    height: 0.355rem;
  }
  .fa-angle-right {
    font-size: 0.3rem;
    color: #d4d4d4;
    -webkit-text-stroke: 0.01rem #fff;
  }
  .bank-name {
    font-size: 0.16rem;
  }
  .bank-num {
    margin-left: 0.05rem;
    .last-num {
      color: $list-color;
    }
    .bank-tip {
      color: $list-color;
      font-size: 0.12rem;
    }
  }
  .grey-bg {
    background: #f6f6f6 !important;
  }
}
</style>



